* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "SF Pro Display", "Roboto", sans-serif;
  overflow-x: hidden;
  color: var(--text-main-color);
  background-color: var(--text-light);
}

button {
  cursor: pointer;
  padding: 0;
  border: none;
  background: transparent;
  outline: none;
}

.overflow-hidden {
  overflow: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

a {
  text-decoration: none;
  outline: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

img {
  display: block;
  max-width: 100%;
}

.basic-flex {
  display: flex;
  align-items: center;
}

.img-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.img-wrapper img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}

.main-color {
  color: var(--main-color);
}

.btn-primary {
  display: block;
  padding: 30px 40px;
  border: 1px solid var(--main-color);
  background-color: var(--main-color);
  color: white;
  text-transform: uppercase;
  font-size: 20px;
  transition: 0.3s ease;
  margin-bottom: -1px;
  margin-top: -1px;
}

.btn-primary:hover,
.btn-primary:focus {
  background-color: transparent;
  color: var(--main-color);
}

.btn-primary.empty-version {
  background-color: transparent;
  color: var(--main-color);
}

.btn-primary.empty-version:hover,
.btn-primary.empty-version:focus {
  background-color: var(--main-color);
  color: white;
}

.title-with-circle {
  position: relative;
  font-size: 80px;
  color: var(--main-color);
}

.title-with-circle::after {
  display: block;
  position: absolute;
  content: "";
  width: 340px;
  height: 160px;
  background-repeat: no-repeat;
  left: -40px;
  top: 50%;
  transform: translateY(-50%);
  z-index: -1;
  background-image: url("../../img/icons/ellipse.svg");
}

.numbered-list li {
  padding: 30px 0;
  border-bottom: 1px solid var(--text-gray);
  border-top: 1px solid var(--text-gray);
  margin-bottom: -1px;
  display: flex;
}

.numbered-list li span {
  margin-right: 40px;
  display: block;
  color: var(--main-color);
  font-weight: bold;
  font-size: 20px;
}

.numbered-list li p {
  font-size: 20px;
  line-height: 1.2;
}

.section-content {
  display: flex;
  justify-content: space-between;
}

.section-content .main-content {
  width: 49%;
}

.section-content .sticky-bar {
  width: 49%;
}

.section-content .sticky-bar > div {
  position: sticky;
  top: 0;
}

.section-content .sticky-bar .img-wrapper {
  top: 0;
  height: 100vh;
  position: sticky;
}

.section-content .sticky-bar .img-wrapper .waves {
  position: absolute;
  z-index: -1;
  top: 13%;
  right: -25%;
  transform: scale(1.3);
}

.btn-circle {
  font-size: 20px;
  color: var(--text-main-color);
  margin-left: 75px;
  height: 150px;
  display: inline-flex;
  align-items: center;
  position: relative;
}

.btn-circle::before {
  position: absolute;
  left: 0;
  top: 0;
  content: "";
  display: block;
  width: 150px;
  height: 150px;
  background-color: var(--main-color);
  border-radius: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
  border: 1px solid var(--main-color);
  transition: 0.3s ease;
}

.btn-circle:hover::before,
.btn-circle:focus::before {
  background-color: transparent;
}

.accordion-list {
  margin-bottom: 100px;
}

.accordion-list .item {
  width: 100%;
  transition: 0.3s ease;
}

.accordion-list .item h6 {
  font-size: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  transition: 0.3s ease;
}

.accordion-list .item h6 img {
  display: block;
  transform: rotate(-90deg);
  transition: 0.3s ease;
  transition: 0.3s ease;
}

.accordion-list .item h6.active {
  color: var(--main-color);
}

.accordion-list .item h6.active img {
  transform: rotate(0);
}

.accordion-list .item li {
  padding: 0;
  border: none;
  color: var(--text-secondary-color);
}

.accordion-list .item li:not(:last-child) {
  margin-bottom: 10px;
}

.accordion-list .item ul,
.accordion-list .item p {
  padding-top: 0;
  height: 0;
  overflow: hidden;
  transition: 0.3s ease;
  opacity: 0;
}

.accordion-list .item ul.active,
.accordion-list .item p.active {
  height: auto;
  padding-top: 30px;
  opacity: 1;
}

.dark-mode .title-with-circle::after {
  filter: invert(1);
  opacity: 1;
}

.secondary-title h4 {
  margin-bottom: 50px;
}

.section {
  margin-bottom: 200px;
}

.container {
  width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
}

.header {
  border-bottom: 1px solid var(--main-color);
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 50px;
}

.header__inner .logo {
  color: var(--text-main-color);
  font-size: 20px;
  display: flex;
}

.header__inner .logo .main-color {
  margin-left: 5px;
}

.header__details {
  display: flex;
  align-items: center;
}

.header__details .number-wrapper {
  margin-right: 30px;
}

.header__details .number-wrapper .main-color {
  font-size: 16px;
  text-align: right;
  margin-bottom: 5px;
}

.header__details .number-wrapper a {
  font-size: 20px;
  font-weight: bold;
  color: var(--text-main-color);
}

#intro h1 {
  font-size: 120px;
  margin: 250px 0;
  color: var(--main-color);
}

#intro h1:after {
  width: 750px;
  height: 300px;
  background-size: contain;
  opacity: 0.7;
  left: -75px;
  top: 30%;
}

#intro .secondary-title h4 {
  font-size: 40px;
  color: var(--text-main-color);
  margin-bottom: 40px;
}

#intro .secondary-title p {
  margin-bottom: 50px;
}

#intro .info {
  margin-bottom: 220px;
}

#intro .info p {
  margin-bottom: 50px;
}

#intro .person-name {
  margin-bottom: 50px;
}

#intro .person-name h3 {
  font-size: 60px;
}

#intro .person-name p {
  font-size: 20px;
}

#intro .awards-list {
  margin-bottom: 80px;
}

#intro .founded-businesses h5 {
  font-size: 26px;
  color: var(--main-color);
  margin-bottom: 30px;
}

#intro .founded-businesses .inner-wrapper {
  display: flex;
}

#intro .founded-businesses a {
  width: 185px;
  height: 185px;
  border-radius: 50%;
  border: 2px solid var(--text-gray);
  background-color: #fff;
  transition: 0.3s ease;
}

#intro .founded-businesses a:not(:last-child) {
  margin-right: 15px;
}

#intro .founded-businesses a:hover,
#intro .founded-businesses a:focus {
  border-width: 2px;
  border-color: #00ad88;
  box-shadow: 0px 0px 30px 0px rgba(0, 173, 136, 0.44);
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: var(--main-color);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--main-color);
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

#about .info {
  margin-bottom: 150px;
}

#about .info p {
  margin-bottom: 40px;
  font-size: 20px;
  line-height: 1.4;
}

#about .course-plan h5 {
  color: var(--main-color);
  font-size: 40px;
  font-weight: bold;
  margin-bottom: 40px;
}

#about .reserved-lessons h5 {
  color: var(--main-color);
  font-size: 40px;
  font-weight: bold;
  margin-bottom: 40px;
}

#about .reserved-lessons-list {
  margin-bottom: 100px;
}

#about .reserved-lessons-list li {
  padding: 30px 0;
  border-bottom: 1px solid var(--text-gray);
  border-top: 1px solid var(--text-gray);
  margin-bottom: -1px;
  display: flex;
  align-items: center;
}

#about .reserved-lessons-list li span {
  margin-right: 15px;
  transform: rotate(-45deg);
  display: block;
  color: var(--main-color);
  font-size: 16px;
}

#about .reserved-lessons-list li p {
  font-size: 30px;
  font-weight: bold;
}

#about .sticky-bar .waves {
  left: -15%;
  right: unset;
  transform: scale(1.5);
}

#course-target {
  margin-bottom: 200px;
}

.course-target-list {
  margin-bottom: 75px;
}

.course-target-list li {
  display: block;
  padding: 30px 0;
}

.course-target-list li span {
  font-size: 120px;
  -webkit-text-fill-color: transparent;
  /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: var(--main-color);
  font-weight: bold;
  margin-bottom: 15px;
}

.course-target-list li h6 {
  font-size: 40px;
  margin-bottom: 25px;
}

.course-target-list li p {
  color: var(--text-secondary-color);
  font-size: 20px;
  line-height: 1.5;
}

.tariffs-list {
  display: flex;
  justify-content: space-between;
}

.tariff-item {
  max-width: 375px;
  background-color: var(--card-bg);
}

.tariff-item .btn-primary {
  text-align: center;
}

.tariff-top {
  padding: 20px 35px;
  background-color: var(--main-color);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.tariff-top h5 {
  text-transform: uppercase;
  font-size: 40px;
  color: white;
  font-weight: 200;
}

.tariff-top .circles {
  display: flex;
  align-items: center;
}

.tariff-top .circles span {
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #fff;
}

.tariff-top .circles span:not(:last-child) {
  margin-right: 5px;
}

.tariff-item-parts {
  padding: 35px;
}

.tariff-item-parts li {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.tariff-item-parts li img {
  margin-right: 15px;
  display: block;
}

.tariff-item-parts li p {
  font-size: 20px;
}

.tariff-item-parts li.unavailable {
  opacity: 0.3;
}

.tariff-price {
  text-align: center;
  margin-bottom: 40px;
}

.tariff-price p {
  font-size: 34px;
  margin-bottom: 10px;
}

.tariff-price span {
  opacity: 0.3;
  font-size: 16px;
}

.dark-mode .tariff-item-parts li.unavailable img {
  filter: invert(1);
}

.partners-wrapper {
  display: flex;
  align-items: center;
}

.partners-wrapper .img-wrapper {
  margin-right: 200px;
}

.partners-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px 70px;
}

.partners-list a {
  font-size: 40px;
  color: var(--text-main-color);
  transition: 0.3s ease;
  border-bottom: 1px solid transparent;
}

.partners-list a:hover,
.partners-list a:focus {
  border-color: var(--text-main-color);
}

.additional-questions {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.additional-questions p {
  font-size: 40px;
  color: var(--text-secondary-color);
  max-width: 600px;
}

.register-form .input-group {
  margin-bottom: 25px;
}

.register-form .input-group label {
  color: var(--text-secondary-color);
  font-size: 16px;
  margin-bottom: 10px;
  display: block;
}

.register-form .input-group input,
.register-form .input-group textarea {
  display: block;
  width: 100%;
  padding: 5px 0;
  border: none;
  outline: none;
  border-bottom: 1px solid var(--text-gray);
  font-size: 24px;
  background-color: transparent;
  color: var(--text-main-color);
}

.register-form .input-group input:focus,
.register-form .input-group textarea:focus {
  border-color: var(--text-main-color);
}

.register-form .input-group textarea {
  resize: none;
  height: 200px;
}

.footer-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 150px;
}

.footer-content .socials-wrapper h6 {
  font-size: 20px;
  color: var(--text-secondary-color);
  font-weight: normal;
  margin-bottom: 35px;
}

.footer-content .socials-wrapper ul {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px 100px;
}

.footer-content .socials-wrapper ul a {
  font-size: 40px;
  color: var(--text-main-color);
  transition: 0.3s ease;
}

.footer-content .socials-wrapper ul a svg {
  margin-left: 10px;
  fill: var(--text-main-color);
  transition: 0.3s ease;
}

.footer-content .socials-wrapper ul a:hover,
.footer-content .socials-wrapper ul a:focus {
  color: var(--main-color);
}

.footer-content .socials-wrapper ul a:hover svg,
.footer-content .socials-wrapper ul a:focus svg {
  fill: var(--main-color);
}

.footer-content .to-top {
  width: 95px;
  height: 95px;
  border-radius: 50%;
  border: 1px solid var(--text-main-color);
  transition: 0.3s ease;
}

.footer-content .to-top:hover,
.footer-content .to-top:focus {
  background-color: var(--main-color);
  border-color: var(--main-color);
}

.footer-content .to-top:hover svg,
.footer-content .to-top:focus svg {
  fill: white;
}

.footer-content .to-top svg {
  fill: var(--text-main-color);
  transition: 0.3s ease;
}

.footer-bottom {
  background-color: var(--card-bg);
  padding: 25px 0;
}

.footer-bottom .basic-flex {
  justify-content: space-between;
}

.footer-bottom .basic-flex a,
.footer-bottom .basic-flex p {
  color: var(--text-main-color);
  font-size: 16px;
}

.popup {
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 5;
  transition: 0.3s ease;
}

.popup.active {
  opacity: 1;
  pointer-events: initial;
}

.popup-mask {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(229, 229, 229, 0.5);
  backdrop-filter: blur(5px);
  z-index: 5;
}

.popup-content {
  z-index: 6;
  background-color: var(--card-bg);
  padding: 20px;
  border-radius: 5px;
}

.popup-content .js-close-modal {
  display: block;
  margin-left: auto;
}

.pop-up-register-form {
  min-width: 300px;
}

.pop-up-register-form h3 {
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
}

.pop-up-register-form .input-group label {
  font-size: 14px;
  color: var(--main-color);
  margin-bottom: 5px;
}

.pop-up-register-form .input-group input {
  font-size: 20px;
}

.pop-up-register-form .input-group textarea {
  height: 100px;
}

.pop-up-register-form .btn-circle {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pop-up-register-form .btn-circle::before {
  left: 50%;
  transform: translate(-50%, -50%);
}

.dark-mode .popup-mask {
  background-color: rgba(51, 53, 62, 0.5);
}